___CSS_LOADER_EXPORT___.push([module.id, `.boardSetup {
	display: flex;
	flex-direction: column;
	align-items: center;
	border-radius: 5px;
	background-color: rgb(0, 0, 0, 0.7);
	color: white;
	padding: 20px;
}

.boardSetup .playerName {
	color: rgb(243, 173, 51);
}

.boardSetup .playerName.second {
	color: rgb(77, 112, 208);
}

.boardSetup > h1,
h2 {
	margin: 0;
	padding: 10px 5px;
	font-family: "RumFont";
}

.setupContainer {
	display: flex;
	align-items: center;
	gap: 20px;
}

.dashboard {
	display: flex;
	flex-direction: column;
	gap: 30px;
	padding: 20px;
}

.dashboard p {
	margin: 0;
	padding: 0;
}

.ships {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
}

#remainingShips,
#shipSizeValue {
	height: 20px;
	display: flex;
	align-items: center;
	gap: 2px;
}

#remainingShips img,
#shipSizeValue img {
	width: 20px;
	height: 20px;
}

.size {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 5px;
}

.orientationSection {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 5px;
}

.orientation {
	background-color: rgb(77, 112, 208);
	border-radius: 5px;
	width: 30px;
}

.orientation:hover {
	cursor: pointer;
	background-color: rgb(77, 112, 208, 0.8);
}

.dashboardBtns {
	display: flex;
	justify-content: center;
	gap: 10px;
}

.dashboardBtns button {
	padding: 10px;
	border-radius: 5px;
	border: none;
	color: white;
	cursor: pointer;
}

.dashboardBtns button:hover {
	opacity: 0.8;
}

.dashboardBtns button.deployBtn {
	background-color: #de5718;
}

.dashboard button.disabled {
	color: #373737;
	background-color: #ccc;
	cursor: not-allowed;
}

.dashboard button.disabled:hover {
	opacity: 1;
}

.dashboardBtns button.clearBtn {
	background-color: #4e4880;
}

.dashboard .errorMsg {
	height: 20px;
	color: red;
}

.loader-container {
	position: fixed;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
	padding: 50px;
	border-radius: 5px;
	background-color: rgb(0, 0, 0, 0.7);
	color: white;
	font-weight: bold;
}

.loader {
	border: 20px white solid;
	border-top: 20px solid rgb(51, 51, 117);
	border-radius: 50%;
	width: 100px;
	height: 100px;
	animation: spin 2s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
