___CSS_LOADER_EXPORT___.push([module.id, `.game {
	display: flex;
	flex-direction: column;
	align-items: center;
	border-radius: 5px;
	gap: 20px;
	background-color: rgb(0, 0, 0, 0.5);
	color: white;
	padding: 20px;
}

.game > h1 {
	margin: 0;
	color: rgb(234, 12, 49);
}

.gameState {
	margin-bottom: 5px;
}

.gameState > span {
	color: rgb(243, 173, 51);
}

.gameState > span.second {
	color: rgb(77, 112, 208);
}

.p1Name {
	color: rgb(243, 173, 51);
}

.p2Name {
	color: rgb(77, 112, 208);
}

.playersContainer {
	display: flex;
	gap: 10px;
	align-items: center;
}

.player1,
.player2 {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
}

.playAgainBtn {
	background-color: #4caf50;
	color: white;
	border: none;
	border-radius: 5px;
	padding: 10px 20px;
	cursor: pointer;
}
