___CSS_LOADER_EXPORT___.push([module.id, `.playerSetup {
	display: flex;
	flex-direction: column;
	align-items: center;
	border-radius: 5px;
	gap: 20px;
	padding: 20px;
	background-color: rgb(0, 0, 0, 0.7);
	color: white;
}

.playerContainer {
	display: flex;
	gap: 20px;
}

.p1,
.p2 {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
}

.p1 > img,
.p2 > img {
	width: 100px;
	height: 100px;
	padding: 0;
	margin: 0;
}

input[type="text"] {
	font-size: large;
	font-weight: bold;
	padding: 10px;
	border-radius: 5px;
}

input[type="radio"],
label {
	margin: 0;
	padding: 0;
	cursor: pointer;
}

.submitBtn {
	padding: 10px 20px;
	border-radius: 5px;
	border: none;
	background-color: #4caf50;
	color: white;
	cursor: pointer;
}
